	import {uniCard, uniPagination} from '@dcloudio/uni-ui'	
	import findApi from '../../api/find.js'
	export default {
        components: {
            uniCard,
            uniPagination
        },			
		data() {
			return {
				selectedOption: 'optionB',
				selected: 'tab1',
				contentData: [],
                // title: '快陪练',
                // extra: '教育科技公司',
                // note: '拓展钢琴陪练业务拓展钢琴陪练业务拓展钢琴陪练业务拓展钢琴陪练业务拓展钢琴陪练业务拓展钢琴陪练业务拓展钢琴陪练业务拓展钢琴陪练业务拓展钢琴陪练业务',
                // thumbnail: require('../../static/logo.png'),
                // isFull: true				
			};
		},
		onLoad () {
			this._getlistData()
		},
		methods:{
			// 获取list 列表数据
			_getlistData () {
				uni.showLoading()
				findApi.getlistData().then(res => {
					uni.hideLoading()
					console.log(res)
					if(res && res.data.code === 1){
						debugger
						uni.showToast({
							title:"数据获取成功"
						})
						this.contentData = res.data.data
					}else{
						uni.hideLoading()
						uni.showToast({
							title:"数据获取失败",
							mask:true,
							duration:2000
						})

					}
				}).catch(err => {
					uni.hideLoading()
					uni.showToast({
						title:"数据获取失败,请重试",
						mask:true,
						duration:2000
					})
				})
			}
		}
	}
